<template>
    <div>
        <el-row class="my-header">
            <el-col :span='6' :offset='18'>
                3.3.19 5121 长短款登记
            </el-col>
        </el-row>
        <el-row>
            <el-col :span='20' :offset="1">
                <el-form ref="mainForm" :model="mainForm" label-width="160px">
                    <el-form-item label="操作员ID" prop="operatorId">
                        <el-input v-model="mainForm.operatorId"></el-input>
                    </el-form-item>
                    <el-form-item label="车站ID" prop="stationId">
                        <el-input v-model="mainForm.stationId"></el-input>
                    </el-form-item>
                    <el-form-item label="长短款类型" prop="longOrShortTypes">
                        <el-radio-group v-model='mainForm.longOrShortTypes'>
                            <el-radio label="1" border size="mini">BOM短款</el-radio>
                            <el-radio label="2" border size="mini">解行短款</el-radio>
                            <el-radio label="3" border size="mini">BOM长款</el-radio>
                            <el-radio label="4" border size="mini">解行长款</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="长短款处理状态" prop="longOrShortStatus">
                        <el-radio-group v-model='mainForm.longOrShortStatus'>
                            <el-radio label="1" border size="mini">未发布</el-radio>
                            <el-radio label="2" border size="mini">待处理</el-radio>
                            <el-radio label="3" border size="mini">已补款</el-radio>
                            <el-radio label="4" border size="mini">已申诉</el-radio>
                            <el-radio label="5" border size="mini">申诉通过</el-radio>
                            <el-radio label="6" border size="mini">申诉拒绝</el-radio>
                        </el-radio-group>
                    </el-form-item>
                     <el-form-item label="长短款金额" prop="longOrShortAmounts">
                        <el-input v-model="mainForm.longOrShortAmounts"></el-input>
                    </el-form-item>
                     <el-form-item label="单据编号" prop="documentNum">
                        <el-input v-model="mainForm.documentNum"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="mainFormSubmit('mainForm')">
                            <i class="iconfont antd-icon-edit-square"></i>
                                登记
                            </el-button>
                        <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    export default {
        name:'',
        data(){
            return {
                mainForm: {
                    operatorId: '',
                    stationId: '',
                    longOrShortTypes: '',
                    longOrShortStatus: '',
                    longOrShortAmounts: '',
                    documentNum: ''
                }
            }
        },
        methods:{
            resetForm(formName) {
                this.$refs[formName].resetFields();   
            },
            mainFormSubmit(formName) {
                const target = this.mainForm
                const vm = this
                this.$refs[formName].validate(function (valid) {
                    if (valid) {
                        vm.$notify({
                                type: 'success',
                                message: '操作成功',
                                duration: 2000
                            })
                        vm.resetForm('mainForm')
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>
<style lang="less">
    .el-radio {
        margin-right: 10px;
    }
</style>