<template>
    <div>
        <el-row style="margin-bottom:10px;">
            <el-col :span='6'>
                <el-button 
                    type="primary" 
                    size="mini"
                    @click="storeOperate('1')">
                    <i class="el-icon-circle-plus-outline"></i>
                    入库</el-button>
                <el-button 
                    type="danger" 
                    size="mini"
                    @click="storeOperate('2')">
                    <i class="el-icon-remove-outline"></i>
                    出库</el-button>
                 <el-button 
                    type="warning"
                    size="mini"
                    @click="storeOperate('3')">
                    <i class="el-icon-edit"/>库存调整</el-button>
            </el-col>
        </el-row>
        <el-table
            ref="singleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column
                type="selection"
                width="55">
            </el-table-column>
            <el-table-column label="编号" width="50" prop="id">
            </el-table-column>
            <el-table-column
                label="库存位置">
                <template slot-scope="scope">{{ scope.row.address }}</template>
            </el-table-column>
            <el-table-column
                prop="type"
                label="库存类型">
            </el-table-column>
            <el-table-column
                prop="numbers"
                label="库存数量"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                prop="status"
                label="库存状态"
            show-overflow-tooltip>
            </el-table-column>
        </el-table>
        <!-- 入库 填写方式  出库填写方式 ，operateType 控制页面显示及选项-->
        <el-dialog
            :title="operateTypeText"
            :visible.sync="storeDiaogVisible"
            width="70%"
            :before-close="handleClose">
            <!-- 认证操作员模块 -->
            <el-row>
                <el-form :model="checkForm" :rules="checkFormRules" ref="checkForm" label-width="130px" class="demo-ruleForm">
                    <el-row>
                        <el-col :span="7">
                            <el-form-item label="认证操作员编号:" prop="id">
                                <el-input v-model="checkForm.id"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="认证操作员密码:" prop="password">
                                <el-input v-model="checkForm.password" type="password"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                             <el-form-item label="变更原因:">
                                <el-select v-model="storeReason" placeholder="请选择">
                                    <el-option
                                    v-for="item in storeReasonOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-row>
            <!-- 入库填写表格 -->
            <el-table :data="storeTableData" 
                class="tb-edit" style="width: 100%" 
                highlight-current-row >
                <el-table-column label="编号" width="50" prop="id">
                </el-table-column>
                <el-table-column label="库存位置" width="180">
                    <template scope="scope">
                        <span>{{scope.row.address}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="库存类型" width="180">
                    <template scope="scope">
                        <span>{{scope.row.type}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" label="库存数量">
                    <template scope="scope">
                        <span>{{scope.row.numbers}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" :label="operateTypeText + afterText + '数量'">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.changeNumbers" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
                 <el-table-column prop="scope" label="备注">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.remark" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="storeDiaogVisible= false">取 消</el-button>
                <el-button type="primary" @click="storeSureChange">提交</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {OPERATE_REASON_ADJUST,CARD_OPERATE_REASON_INSTORE,CARD_OPERATE_REASON_OUTSTORE} from '@/assets/static.js'
  export default {
    data() {
        return {
            tableData: [{
                    id:'1',
                    address: '新郑机场',
                    type: '预付值-1元',
                    numbers:'1',
                    status: '已上溢',
                    label:'编号',
                    width:'50',
                },{
                    id:'2',
                    address: '新郑机场',
                    type: '预付值-2元',
                    numbers:'1',
                    status: '已上溢',
                    label:'库存位置',
                    width:'150'
                },
                {
                    id:'3',
                    address: '新郑机场',
                    type: '预付值-3元',
                    numbers:'1',
                    status: '已上溢',
                    label:'库存数量',
                    width:'150'
                },
                {
                    id:'4',
                    address: '新郑机场',
                    type: '预付值-4元',
                    numbers:'1',
                    status: '已上溢',
                    label:'库存状态',
                    width:'150'
                }
            ],
            multipleSelection: [],
            dialogVisible:false,
            storeTableData:[

            ],
            storeDiaogVisible:false,
            operateType:'',
            storeReason:'',
            checkForm:{
                id: '',
                password: '',
            },
            checkFormRules:{
                id:[
                    { required: true, message: '请输入编号', trigger: 'blur' },
                ],
                password:[
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ]
            },
        }
    },
    computed:{
        storeReasonOptions() {
            this.storeReason = ''
            if (this.operateType === '1') {
                return CARD_OPERATE_REASON_INSTORE
                
            }
            else if (this.operateType === '2'){
                return CARD_OPERATE_REASON_OUTSTORE
            }
            else if (this.operateType === '3'){
                return OPERATE_REASON_ADJUST
            }
            else {

            }
        },
        operateTypeText() {
            if (this.operateType === '1') {
                return '入库'   
            }
            else if(this.operateType === '2'){
                return '出库'
            }
            else if (this.operateType === '3') {
                return '库存调整'
            }
        },
        afterText() {
            if (this.operateType === '1') {
                return ''   
            }
            else if(this.operateType === '2'){
                return ''
            }
            else if (this.operateType === '3') {
                return '后'
            }
        }
    },
    methods: {
        // 1出入库 操作
        storeOperate(index) {
            if (this.multipleSelection.length === 0) {
                    this.$message({
                        type:'error',
                        message:'请选择',
                        duration:2000
                    })
                    return
                }
            this.operateType = index
            this.storeDiaogVisible = true
            const target = this.multipleSelection.map(item => {
                return Object.assign({},item,{
                    changeNumber:'',
                    remark:''
                })
            })
            this.storeTableData = target
        },
        handleClose(done) {
            done();
        },
        // inStoreSureChange
        storeSureChange() {
            this.storeDiaogVisible = false
            this.storeTableData.map(item => {
                const id = item.id
                let value
                if (!item.changeNumbers) {
                    item.changeNumbers = 0
                }
                if(this.operateType ==='1') {
                    value = item.numbers*1 + item.changeNumbers*1
                }
                else if (this.operateType ==='2') {
                    value = item.numbers*1 - item.changeNumbers*1
                }
                else if (this.operateType === '3') {
                    value = item.changeNumbers
                }
                this.tableData.map(row => {
                    if(row.id === id) {
                        row.numbers = value
                    }
                })
            })
            this.$axios({

            })
            this.$notify({
                message:'操作成功',
                type:'success'
            })
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            this.changeableTableData = val
        },
        handleCurrentChange(row, event, column) {
            // console.log(row, event, column, event.currentTarget)
        },
        handleDelete(index, row) {
            this.changeableTableData.splice(index,1)
        }
    }
  }
</script>