<template>
    <transition name="el-zoom-in-top">
        <div>
            <!-- 操作栏 -->
            <el-form ref="operateForm" :model="operateForm" label-width="80px">
                <el-row>
                    <el-col :span='10'>
                        <el-form-item>
                            <el-button 
                            type="primary" 
                            size="mini"
                            @click="operateClick('1')">
                            <i class="el-icon-circle-plus-outline"></i>
                            收益款入库</el-button>
                        <el-button 
                            type="danger" 
                            size="mini"
                            @click="operateClick('2')">
                            <i class="el-icon-remove-outline"></i>
                            收益款出库</el-button>
                        <el-button 
                            type="warning"
                            size="mini"
                            @click="operateClick('3')">
                            <i class="el-icon-edit"/>收益款库存调整</el-button>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="库存状态">
                            <el-select v-model="operateForm.storeStatusSelect">
                                <el-option
                                    v-for="item in storeStatusOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="状态">
                            <el-select v-model="operateForm.statusSelect">
                                <el-option
                                    v-for="item in statusOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                
            </el-form>
            <!-- 主体表格数据 -->
           <el-table
                ref="mainTableData"
                :data="mainTableData"
                @selection-change="handleSelectionChange"
                tooltip-effect="dark"
                style="width: 100%">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column 
                    v-for="item in mainTableHeader"
                    :key="item.prop"
                    :label="item.label" 
                    :width="item.width" 
                    :prop="item.prop">
                </el-table-column>
            </el-table>
            <!-- 调整输入dialog -->
            <el-dialog
                :title="'收益款'+operateTypeText"
                :visible.sync="mainDiaogVisible"
                width="70%">
                <!-- 认证操作员模块 -->
                <el-row>
                    <el-form :model="checkForm" :rules="checkFormRules" ref="checkForm" label-width="130px" class="demo-ruleForm">
                        <el-row>
                            <el-col :span="7">
                                <el-form-item label="认证操作员编号:" prop="id">
                                    <el-input v-model="checkForm.id"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="认证操作员密码:" prop="password">
                                    <el-input v-model="checkForm.password" type="password"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="10">
                                <el-form-item label="变更原因:">
                                    <el-select v-model="operateReason" placeholder="请选择">
                                        <el-option
                                        v-for="item in operateReasonOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </el-row>
                <!--  -->
                <!-- 入库填写表格 -->
                <el-table 
                    :data="dialogTableData" 
                    class="tb-edit" 
                    style="width: 100%" 
                    highlight-current-row >
                    <el-table-column label="编号" width="50" prop="id">
                    </el-table-column>
                    <el-table-column label="库存位置" prop='storePosition'>
                    </el-table-column>
                    <el-table-column label="库存类型" prop="storeType">
                    </el-table-column>
                    <el-table-column label="库存状态" prop="storeStatus">
                    </el-table-column>
                    <el-table-column label="库存余额" prop="storeRemainingSum">
                    </el-table-column>
                    <el-table-column label="当前状态" prop="storeRemainingSum">
                    </el-table-column>
                    <el-table-column prop="scope" :label="operateTypeText + afterText + '金额'">
                        <template scope="scope">
                            <el-input size="small" v-model="scope.row.changeNumbers" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                        </template>
                    </el-table-column>
                    <el-table-column prop="scope" label="备注">
                        <template scope="scope">
                            <el-input size="small" v-model="scope.row.remark" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                        </template>
                    </el-table-column>
                </el-table>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="mainDiaogVisible= false">取 消</el-button>
                    <el-button type="primary" @click="operateSure">提交</el-button>
                </span>
            </el-dialog>
        </div>
    </transition>
</template>
<script>
import { PROCEEDS_OPERATE_REASON_INSTORE,PROCEEDS_OPERATE_REASON_OUTSTORE,OPERATE_REASON_ADJUST} from '@/assets/static.js'
export default {
    components:{
        
    },
    data() {
        return {
            operateType:'',
            operateForm:{
                statusSelect:'',
                storeStatusSelect:'',
            },
            statusOptions:[
                {
                    value:'1',
                    label:'所有'
                },
                {
                    value:'2',
                    label:'正常'
                },
                {
                    value:'3',
                    label:'将上溢'
                },
                {
                    value:'4',
                    label:'已上溢'
                },
                {
                    value:'5',
                    label:'将下溢'
                },
                {
                    value:'2',
                    label:'已下溢'
                },
            ],
            storeStatusOptions:[
                 {
                        value:'1',
                        label:'所有'
                    },
                    {
                        value:'2',
                        label:'余额'
                    },
            ],
            mainTableHeader:[
                {
                    prop:'id',
                    label:'序号',
                    width:60,
                },
                {
                    prop:'storePosition',
                    label:'库存位置',
                },
                {
                    prop:'storeType',
                    label:'库存类型',
                },
                {
                    prop:'storeStatus',
                    label:'库存状态',
                },
                {
                    prop:'storeRemainingSum',
                    label:'库存余额',
                },
                {
                    prop:'status',
                    label:'状态',
                },
                 {
                    prop:'updateTime',
                    label:'更新时间',
                }
            ],
            mainTableData: [
                {
                    id:'1',
                    storePosition: '新郑机场',
                    storeType: '备用金',
                    storeStatus:'余额',
                    storeRemainingSum: '2000',
                    status:'已上溢',
                    updateTime:'50',
                },
            ],
            multipleSelection:[],
            mainDiaogVisible:false,
            operateReason:'',
            checkForm:{
                id: '',
                password: '',
            },
            checkFormRules:{
                id:[
                    { required: true, message: '请输入编号', trigger: 'blur' },
                ],
                password:[
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ]
            },
            dialogTableData:[

            ]
        }
    },
    computed:{
        operateTypeText() {
            if (this.operateType*1 === 1) {
                return '入库'
            }
            else if (this.operateType*1 ===2) {
                return '出库'
            }
            else if (this.operateType === 3) {
                return '库存调整'
            }
        },
        operateReasonOptions() {
            if (this.operateType*1 === 1) {
                return PROCEEDS_OPERATE_REASON_INSTORE
            }
            else if (this.operateType*1 ===2) {
                return PROCEEDS_OPERATE_REASON_OUTSTORE
            }
            else if (this.operateType === 3) {
                return PROCEEDS_OPERATE_REASON_ADJUST
            }
        },
        afterText() {
            return this.operateType*1 ===3 ? '后' : ''
        }
    },
    methods:{
        operateClick(index) {
            index = index*1
             if (this.multipleSelection.length === 0) {
                this.$message({
                    type:'error',
                    message:'请选择',
                    duration:2000
                })
                return
            }
            this.operateReason = ''
            this.mainDiaogVisible = true
            this.operateType = index
            const target = this.multipleSelection.map(item => {
                return Object.assign({},item,{
                    changeNumbers:'',
                    remark:'',
                })
            })
            this.dialogTableData = target
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleEdit(index,row) {
            console.log(this.dialogTableData)

            this.dialogTableData.slice(index,1)
        },
        operateSure() {
            this.mainDiaogVisible = false
            this.dialogTableData.map(item => {
                const id = item.id
                let value
                if (!item.changeNumbers) {
                    item.changeNumbers = 0
                }
                if(this.operateType*1 === 1) {
                    value = item.storeRemainingSum*1 + item.changeNumbers*1
                }
                else if (this.operateType*1 === 2) {
                    value = item.storeRemainingSum*1 - item.changeNumbers*1
                }
                else if (this.operateType*1 === 3) {
                    value = item.changeNumbers
                }
                this.mainTableData.map(row => {
                    if(row.id === id) {
                        row.storeRemainingSum = value
                    }
                })
            })
            this.$axios({

            })
            this.$notify({
                message:'操作成功',
                type:'success'
            })
        }
    }
}
</script>
