<template>
    <div>
        <el-row class="my-header">
            <el-col :span='6' :offset='18'>
                增加3项管理内容，不需要上传到ancc 1/3
            </el-col>
        </el-row>
         <!-- 添加button -->
        <el-row class="mrbt">
            <el-col :span="2">
                <el-button size="mini" type='warning' icon="el-icon-plus" @click="handleAddTicket">添加</el-button>
            </el-col>
        </el-row>
        <el-dialog title="票卡明细录入" :visible.sync="dialogFormVisible">
                <el-form 
                    :model="dialogFormData" 
                    :rules="dialogFormDataRules"
                    ref='dialogFormData'
                    status-icon 
                    label-width="120px">
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="票卡物理类型" prop='cardPhysicsType'>
                                <el-input v-model="dialogFormData.cardPhysicsType" prop=''></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="票卡发行商ID" prop='cardIssue'>
                                <el-input v-model.number="dialogFormData.cardIssue" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="车票产品种类" prop='cardType'>
                                <el-input v-model.number="dialogFormData.cardType" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="预赋值金额" prop='prePaymentMoney'>
                                <el-input v-model="dialogFormData.prePaymentMoney" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="车票状态" prop='cardStatus'>
                                <el-input v-model="dialogFormData.cardStatus" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="车票数量" prop='cardNumber'>
                                <el-input v-model="dialogFormData.cardNumber" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="运输方式" prop='transportationMode'>
                                <el-input v-model="dialogFormData.transportationMode" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="票箱编号" prop='boxId'>
                                <el-input v-model="dialogFormData.boxId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('dialogFormData')">
                        重置数据
                    </el-button>
                    <el-button type="primary" @click="dialogFormSubmit('dialogFormData')">
                        添加
                    </el-button>
                </div>
        </el-dialog>
        <el-row class='table-history mrbt' v-show="tableVisible">
            交接班票卡明细
            <el-table :data="dialogForm2table" style="width: 100%" size='mini' max-height="250">
                <el-table-column fixed prop="cardPhysicsType" label="票卡物理类型">
                </el-table-column>
                <el-table-column prop="cardIssue" label="票卡发行商ID">
                </el-table-column>
                <el-table-column prop="cardType" label="车票产品种类">
                </el-table-column>
                <el-table-column prop="prePaymentMoney" label="预赋值金额" >
                </el-table-column>
                <el-table-column prop="cardStatus" label="车票状态" >
                </el-table-column>
                <el-table-column prop="cardNumber" label="车票数量" >
                </el-table-column>
                <el-table-column prop="transportationMode" label="运输方式" >
                </el-table-column>
                <el-table-column prop="boxId" label="票箱编号" >
                </el-table-column>
                <el-table-column fixed="right" label="操作" >
                    <template slot-scope="scope">
                        <el-button @click.native.prevent="deleteRow(scope.$index, dialogForm2table)" type="danger" size="small">
                            移除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>
        <el-row v-show="tableVisible">
            <el-col :span="16" :offset="2">
                <el-form ref="mainForm" :model="mainForm" label-width="190px">
                    <el-form-item label="操作员姓名" prop='operatorName'>
                        <el-input v-model="mainForm.operatorName"></el-input>
                    </el-form-item>
                     <el-form-item label="操作员工号" prop='operatorId'>
                        <el-input v-model="mainForm.operatorId"></el-input>
                    </el-form-item>
                    <el-form-item label="票款（收益款+备用金）" prop="tickets">
                        <el-input v-model="mainForm.tickets"></el-input>
                    </el-form-item>
                    <el-form-item label="五角" prop="fiveCorners">
                        <el-input v-model="mainForm.fiveCorners"></el-input>
                    </el-form-item>
                    <el-form-item label="一元" prop="oneYuan">
                        <el-input v-model="mainForm.oneYuan"></el-input>
                    </el-form-item>
                    <el-form-item label="五元" prop="fiveYuan">
                        <el-input v-model="mainForm.fiveYuan"></el-input>
                    </el-form-item>
                    <el-form-item label="十元" prop="tenYuan">
                        <el-input v-model="mainForm.tenYuan"></el-input>
                    </el-form-item>
                    <el-form-item label="二十元" prop="twentyYuan">
                        <el-input v-model="mainForm.twentyYuan"></el-input>
                    </el-form-item>
                    <el-form-item label="备用金" prop="reserveFund">
                        <el-input v-model="mainForm.reserveFund"></el-input>
                    </el-form-item>
                    <el-form-item label="交接班时间" prop="handoverTime">
                        <el-date-picker
                            v-model="mainForm.handoverTime"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="mainFormSubmit('mainForm')">
                            <i class="iconfont antd-icon-edit-square"></i>
                            执行
                        </el-button>
                        <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                    </el-form-item>
                </el-form>
           </el-col>
       </el-row>
    </div>
</template>

<script>
export default {
    name:'Relieve',
    data() {
        return {
            dialogFormVisible: false,
            dialogFormData:{
                cardPhysicsType:'',
                cardIssue:'',
                cardType:'',
                prePaymentMoney :'',
                cardStatus :'',
                cardNumber :'',
                transportationMode:'',
                boxId:''
            },
            dialogFormDataRules:{},
            dialogFormVisible: false,
            dialogForm2table:[],
            mainForm: {
                operatorName: '',
                operatorId: '',
                tickets: '',
                reserveFund: '',
                fiveCorners: '',
                oneYuan: '',
                fiveYuan: '',
                tenYuan: '',
                twentyYuan: '',
                ticketCard: '',
                cardDetails:[],
                handoverTime:''
            }
        }
    },
    computed:{
        tableVisible() {
            return this.dialogForm2table.length === 0 ? false : true
        }
    },
    methods: {
        handleAddTicket() {
            this.dialogFormVisible = true
        },
        dialogFormSubmit(formName) {
            let addTableData = this.dialogFormData
            let target = Object.assign({}, addTableData)
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    vm.dialogForm2table.push(target)
                    vm.resetForm('dialogFormData')
                    vm.dialogFormVisible = false
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            
            this.$refs[formName].resetFields();
            
        },
        deleteRow(index, rows) {
            rows.splice(index, 1);
        },
        mainFormSubmit(formName) {
            const target = this.mainForm
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    vm.$notify({
                        type: 'success',
                        message: '操作成功',
                        duration: 2000
                    })
                    vm.resetForm('mainForm')
                    vm.dialogForm2table = []
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },

    }
}
</script>
<style scoped>

</style>

