                        message: '操作成功',
                        duration: 2000
                    })
                    vm.resetForm('mainForm')
                    // vm.$axios('/success').then(res => {
                       
                    // }).catch(err => {
                    //     vm.$notify({
                    //         type: 'error',
                    //         message: '操作失败',
                    //         duration: 2000
                    //     })
                    // })
                    // vm.dialogForm2table.push(target)
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
    }
}
</script>
