<template>
    <div>
        <el-row class="my-header">
            <el-col :span='6' :offset='18'>
                增加3项管理内容，不需要上传到ancc 2/3
            </el-col>
        </el-row>
       <el-row>
           <el-col :span="16" :offset="2">
                <el-form ref="mainForm" :model="mainForm" label-width="190px">
                    <el-form-item label="配发数量" prop='allotQuantity'>
                        <el-input v-model="mainForm.allotQuantity"></el-input>
                    </el-form-item>
                     <el-form-item label="备用数量" prop='reserQuantity'>
                        <el-input v-model="mainForm.reserQuantity"></el-input>
                    </el-form-item>
                    <el-form-item label="日常使用数量" prop='dailyUsageQuantity'>
                        <el-input v-model="mainForm.dailyUsageQuantity"></el-input>
                    </el-form-item>
                     <el-form-item label="报损数量" prop='lossReportQuantity'>
                         <el-input v-model="mainForm.lossReportQuantity"></el-input>
                    </el-form-item>
                     <el-form-item>
                        <el-button type="primary" @click="mainFormSubmit('mainForm')">
                            <i class="iconfont antd-icon-edit-square"></i>
                            登记
                        </el-button>
                        <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                    </el-form-item>
                </el-form>
           </el-col>
       </el-row>
    </div>
</template>

<script>
export default {
    name:'Relieve',
    data() {
        return {
            mainForm: {
                allotQuantity: '',
                reserQuantity: '',
                dailyUsageQuantity: '',
                lossReportQuantity: ''
            }
        }
    },
    methods: {
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        mainFormSubmit(formName) {
            const target = this.mainForm
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    vm.$notify({
                        type: 'success',
                        message: '操作成功',
                        duration: 2000
                    })
                    vm.resetForm('mainForm')
                    // vm.$axios('/success').then(res => {
                       
                    // }).catch(err => {
                    //     vm.$notify({
                    //         type: 'error',
                    //         message: '操作失败',
                    //         duration: 2000
                    //     })
                    // })
                    // vm.dialogForm2table.push(target)
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
    }
}
</script>
