<template>
    <div>
        <el-form ref="ticketDialogForm" :model="ticketDialogForm" label-width="100px">
            <el-row>
                <el-col :span="6">
                    <el-form-item label="当前车站">
                        <el-input v-model="ticketDialogForm.station"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="运营时间">
                        <el-input v-model="ticketDialogForm.station"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="票卡类型">
                        <el-input v-model="ticketDialogForm.ticketType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="票卡面值">
                        <el-input v-model="ticketDialogForm.ticketPrice"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="票卡数量">
                        <el-input v-model="ticketDialogForm.ticketNumbers"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="">
                        <el-button 
                            @click="addRows"
                            type="warning" size="mini"> 
                            <i class="el-icon-plus"></i>
                            添加
                        </el-button>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row v-show="ticketDialogTableVisible">
                <el-col :span="22">
                    <el-form-item label="票卡详情">
                            <el-table
                            :data="ticketDialogTableData"
                            border 
                            size='mini'
                            style="width: 100%">
                            <el-table-column
                                prop="ticketType"
                                label="票卡类型">
                            </el-table-column>
                            <el-table-column
                                prop="ticketPrice"
                                label="票卡面值（元）">
                            </el-table-column>
                            <el-table-column
                                prop="ticketNumbers"
                                label="票卡数量（张）">
                            </el-table-column>
                            <el-table-column
                                width="70"
                                label="删除操作">
                                <template slot-scope="scope">
                                    <el-button 
                                        @click="handleRowDelete(scope.$index, ticketDialogTableData)"
                                        icon="el-icon-delete" type="danger" size="mini">
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-form-item>
                    
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="备用金">
                        <el-input v-model="ticketDialogForm.station"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="售票员">
                        <el-input v-model="ticketDialogForm.station"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="密码">
                        <el-input v-model="ticketDialogForm.station"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-form-item>
                <el-button type="primary" >提交</el-button>
                <el-button @click='handleDialogClose'>取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            ticketDialogForm:{

            },
            ticketDialogTableData:[
                // {
                //     ticketType:'1',
                //     ticketPrice:'1111',
                //     ticketNumbers:'2222'
                // }
            ]
        }
    },
    computed:{
        ticketDialogTableVisible() {
            return this.ticketDialogTableData.length === 0 ? false :true
        }
    },
    methods:{
        addRows() {
            const ticketType = this.ticketDialogForm.ticketType
            const ticketPrice = this.ticketDialogForm.ticketPrice
            const ticketNumbers = this.ticketDialogForm.ticketNumbers
            this.ticketDialogTableData.push({
                ticketType,
                ticketPrice,
                ticketNumbers
            })
        },
        handleRowDelete(index,rows) {
            rows.splice(index, 1);
        },
        handleDialogClose() {
            this.$emit('my-dialog-close')
        }
    }
}
</script>
